.block.article-title {
  // sass-lint:disable no-invalid-hex
  @at-root body.page-rubric #zone4 .block.block-page.edito.edito-custom, /* hub exposant (milieu de page) */
  body.front &, /* homepage */
  body.page-rubric #zone2 > .article-wrapper > &, /* hub exposant (haut de page) */
  body.page-rubric #zone4 > & /* hub visiteur */ {
    // sass-lint:enable no-invalid-hex
    margin-left: auto;
    margin-right: 0;
    margin-top: 0;
    .article-intro {
      font-size: 20px;
      font-weight: 300;
      margin: 0;
      p, .p-like, ul, ol, dl, blockquote, pre, td, th, label, textarea, caption, details, figure {
        line-height: 30px;
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
      .lien1 {
        @extend %button;
      }
    }

    &::after {
      display: none;
    }

    & > .inside {
      @include mq_min_width($desktop-medium) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }
      .cx-lazy-load {
        object-fit: cover;
        font-family: 'object-fit: cover;';
        float: none;
        margin: 0 auto 40px;
        width: auto;
        max-width: 100%;
        max-height: 400px;
        @include mq_min_width($desktop-medium) {
          margin: 0;
          width: 503px;
          flex-shrink: 0;
          max-width: none;
          max-height: none;
        }
      }
      .at-content {
        width: 100%;
        margin: 0;
        @include mq_min_width($desktop-medium) {
          margin-left: 60px;
          padding: 28px 0;
        }
      }
    }
  }
  body.front .block.article-title { /* homepage */
    max-width: 1175px;
  }
  // sass-lint:disable no-invalid-hex
  @at-root body.page-rubric #zone4 .block.block-page.edito.edito-custom /* hub exposant (milieu de page) */ {
    // sass-lint:enable no-invalid-hex
    margin-top: 30px;
    margin-bottom: 30px;
    @include mq_min_width($tablet-small) {
      margin-top: 40px;
      margin-bottom: 40px;
    }
    @include mq_min_width($tablet-medium) {
      margin-top: 60px;
      margin-bottom: 60px;
    }
    @include mq_min_width($desktop-small) {
      margin-top: 80px;
      margin-bottom: 80px;
    }
    @include mq_min_width($desktop-medium) {
      margin-top: 100px;
      margin-bottom: 100px;
    }
  }
}
