@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.node1318534 {

  #zone1 .edito {
    text-align: center;

    h2 {
      @extend .main-title;
    }

    p {
      font-size: 20px;
      line-height: 1.5;

      em {
        font-size: 16px;
      }
    }
  }

  .contacts-list {
    background-color: $lightgrey-3;
    margin: 70px -63px;
    padding: 51px 55px;

    .block-title {
      @extend .main-title;
      font-size: 40px;
    }

    .cl-content {
      @extend %commercial-card-wrapper;
    }

    .cl-item {
      @extend %commercial-card;
      flex-direction: column;
      max-width: 291px;
      margin: 15px 0;

      &-photo {
        height: 212px;
        overflow: hidden;

        > img {
          width: 100%;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
      }

      &-content {
        padding: 25px 30px 15px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        min-height: 138px;
        background-color: #fff;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &-title {
        @extend %commercial-card-title;
        order: 2;
        margin-bottom: 5px;
      }

      &-subtitle {
        order: 3;
        font-family: $font-1;
        font-weight: 300;
        text-transform: lowercase;

        &:first-letter {
          text-transform: capitalize;
        }
      }

      &-email,
      &-phone {
        display: none;
      }

      &-social {
        @extend %commercial-card-social;
        margin-bottom: 11px;
      }
    }
  }

  .quicklinks-cta--large {
    ul {
      flex-direction: row-reverse;
    }

    .ql-item {
      @media screen and (min-width: $tablet-medium) {
        width: 343px;
      }
    }
  }
}
