@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.page-highlight {
  position: relative;
  z-index: 0;
  margin: 60px -25px 0;

  @include mq_min_width($desktop-small) {
    display: flex;
    margin: 80px 0 0;
    padding: 80px 0;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    bottom: -649px;
    display: none;
    width: 100vw;
    min-height: 100%;
    background-color: $lightgrey-3;
    transform: translateX(-50%);

    @include mq_min_width($desktop-small) {
      display: block;
    }
  }

  .block.edito {
    display: flex;
    flex-direction: column;
    margin: 0 0 36px;
    flex-shrink: 0;
    background-color: $lightgrey-3;

    @include mq_min_width($desktop-small) {
      width: percentage(673px / $wrapper-size);
      min-height: percentage(411px / $wrapper-size);
      margin-bottom: 0;
      background-color: #fff;
    }

    .inside {
      height: 100%;
      font-size: 20px;
      line-height: 30px;
      padding: 0 0 40px;

      @include mq_min_width($tablet-medium) {
        padding-right: percentage(59px / 673px);
        padding-bottom: 0;
      }

      > *:not(img) {
        margin-left: 25px;
        margin-right: 25px;

        @include mq_min_width($tablet-medium) {
          margin-left: percentage(340px / 673px);
        }
      }
    }

    .cx-lazy-load {
      min-width: 100%;
      max-width: 100vw;
      height: 268px;
      object-fit: cover;
      font-family: 'object-fit: cover;';
      @include mq_min_width($tablet-medium) {
        float: left;
        width: percentage(290px / 673px);
        min-width: 0;
        height: 100%;
      }
    }

    h2 {
      @extend .main-title;
      margin-top: 32px;
      font-size: 30px;
      line-height: 40px;

      @include mq_min_width($tablet-medium) {
        margin-top: percentage(62px / 673px);
        font-size: 40px;
        line-height: 60px;
      }
    }

    .btn-primary {
      @extend %button;
      margin-top: 27px;
    }
  }
}
