@import '../includes/variables';
@import '../includes/mixins';

.site-banner {
  position: fixed;
  z-index: 20;
  top: 0;
  right: 0;
  left: 0;
  padding: 0;
  color: currentColor;
  background: #fff;
  border-bottom: 1px solid #e3e9ea;

  @include mq_min_width($tablet-medium) {
    position: fixed;
  }

  &::after { // top-header background
    content: '';
    display: none;
    height: 55.2px;
    width: calc(100vw - 387px);
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    opacity: 0.35;
    background-color: $lightgrey-5;

    @include mq_min_width($tablet-medium) {
      display: block;
    }

    @include mq_min_width($desktop-small) {
      height: 37px;
    }
  }

  &.is-stuck {
    padding: 0;
  }

  .inside {
    max-width: 1360px;
    padding-top: 0;
  }
}


.is-contentPusher {
  display: none;
}
