body.catalogue {
  font-family: $font-1-alt;
  color: $textcolor-2;
  background-color: #fff;
  .form-select {
    border-radius: 7px !important;
    border: 0;
    outline: 0 !important;
    background: $lightgrey-5 !important;
    select {
      font-family: $font-1-alt;
      color: #8C909E;
      font-size: 16px;
      font-weight: 300;
      max-width: none;
      width: 100%;
      min-height: 0;
      height: 38px;
      outline: 0;
      padding: 0 50px 0 15px !important;
      box-shadow: none !important;
      &:not([disabled]) {
        cursor: pointer;
      }
      &[disabled] {
        cursor: not-allowed;
      }
      option {
        color: $textcolor-3;
      }
    }
    &:before {
      content: none !important;
    }
    &:after {
      content: '\1f51d';
      @include icomoon;
      color: #9ca4bb;
      border: 0;
      width: 20px;
      height: 20px;
      transform: rotate(-180deg) translateY(72%);
      font-size: 8px;
      right: 20px;
      top: 50% !important;
    }
  }
  #zone3 {
    display: none;
  }
  .block.edito.mod-catal {
    margin-top: 20px;
    h1 {
      margin: 0;
      padding: 0;
      font-family: $font-2;
      font-weight: 900;
      font-size: 26px;
      line-height: 45px;
      text-align: center;
      border: 0;
      color: $textcolor-3;
      @include mq_min_width($tablet-medium) {
        line-height: 60px;
        font-size: 42px;
      }
    }
    & + .exhibitor-search-form {
      margin-top: 2.5px;
    }
  }
  .exhibitor-search-form {
    .esf-form-zone {
      margin: 0;
      padding: 0;
      border: 0;
      background: transparent;
      border-radius: 0;
      .esf-intro-title {
        margin: 0;
        font-family: $font-1-alt;
        font-weight: 300;
        font-size: 20px;
        line-height: 31px;
        color: $textcolor-2;
        text-align: center;
        & + .esf-search-line {
          margin-top: 34px;
        }
      }
      .esf-search-line {
        margin: 0 auto;
        width: 100%;
        padding-bottom: 50px;
        align-items: center;
        flex-direction: column;
        justify-content: flex-start;
        @include mq_min_width($desktop-small) {
          max-width: 95%;
          justify-content: center;
          flex-direction: row;
          padding-bottom: 91px;
        }
        @include mq_min_width($desktop-medium) {
          max-width: 77.32%;
        }
        &:before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
          width: 100vw;
          height: 1000px;
          z-index: -1;
          background: $lightgrey-3;
        }
        .form-select {
          background: #fff !important;
          width: 100%;
          flex-grow: 0;
          flex-shrink: 0;
          max-width: 365px;
          margin: 0 0 30px;
          @include mq_min_width($desktop-small) {
            margin: 0;
            flex-grow: 1;
            flex-shrink: 1;
            max-width: 100%;
            width: 0;
          }
          select {
            padding: 16px 50px 16px 23px !important;
            height: auto;
          }
          &:after {
            transform: rotate(-180deg) translateY(55%);
            font-size: 11px;
          }
        }
        #esf_simple_search {
          border: 0;
          font-family: $font-1-alt;
          border-radius: 7px;
          font-style: normal;
          padding: 18px 23px 16px;
          font-size: 16px;
          font-weight: 300;
          line-height: 24px;
          color: $textcolor-3;
          flex-grow: 0;
          flex-shrink: 0;
          width: 100%;
          max-width: 365px;
          margin: 0;
          @include mq_min_width($desktop-small) {
            flex-grow: 1;
            flex-shrink: 1;
            width: auto;
            max-width: 100%;
            margin: 0 30px;
          }
          @include placeholder {
            color: #8C909E;
          }
        }
        #esf-btn-search {
          @extend %button;
          width: auto;
          outline: 0;
          margin: 40px 0 0;
          @include mq_min_width($desktop-small) {
            margin: 0;
          }
        }
        #esf_new_search_new {
          @extend %button-underline;
          display: block;
          margin-top: 25px;
          bottom: auto;
          right: auto;
          @include mq_min_width($desktop-small) {
            margin-top: 0;
            position: absolute;
            bottom: 51px;
            right: 0;
          }
          .esf-search-plus-txt {
            text-decoration: none;
          }
          &:after {
            padding: 0;
          }
        }
      }
      .esf-advanced-search {
        margin: 30px 0 0;
        background: transparent;
        text-align: center;
        overflow: hidden;
        @include mq_min_width($desktop-small) {
          text-align: left;
        }
        .esf-as-header {
          position: relative;
          top: auto;
          height: auto;
          outline: 0;
          @extend %button;
          p {
            margin: 0;
            font-size: inherit;
            font-weight: inherit;
            font-family: inherit;
            line-height: inherit;
          }
          &:before,
          &:after {
            content: none;
          }
        }
        .esf-as-filters {
          border: 1px solid #DDE1E6;
          position: relative;
          top: 25px;
          margin-bottom: 25px;
          .esf-as-list-filters,
          .esf-as-filter-panel {
            margin-top: 0;
          }
        }
      }
    }
    #esf-results-zone {
      background: transparent;
      border: 0;
      margin-top: 30px;
      .esf-results-alpha-filter {
        padding: 0;
        color: $textcolor-2;
        .esf-results-filter-alpha {
          @include mq_min_width($desktop-medium) {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
          }
          .esf-results-filter-label {
            font-family: $font-1;
            font-size: 20px;
            font-weight: 600;
            line-height: 31px;
            margin: 0 0 15px;
            display: block;
            text-align: center;
            @include mq_min_width($desktop-medium) {
              margin: 0 20px 0 0;
              text-align: left;
            }
          }
          .esf-results-alpha-list {
            user-select: none;
            display: flex;
            align-items: stretch;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 0 -10px;
            @include mq_min_width($desktop-medium) {
              justify-content: flex-end;
              margin: 0 0 0 auto;
            }
            & > li:not(.js-hidden) {
              margin: 0 0 10px;
              @include mq_min_width($desktop-medium) {
                margin: 0;
              }
              letter-spacing: normal;
              font-size: 16px;
              font-weight: 500;
              font-family: $font-1;
              border: 1px solid rgba($textcolor-2, 0.5);
              border-radius: 8px;
              color: rgba($textcolor-2, 0.5);
              text-align: center;
              padding: 5px 6.2px;
              line-height: 24px;
              min-width: 26px;
              background: #fff;
              cursor: not-allowed;
              &[data-letter='123'] {
                border-radius: 10px;
                a {
                  &:before {
                    border-radius: 10px;
                  }
                }
              }
              &:not(:first-of-type):not(:nth-child(1)) {
                margin-left: 8px;
              }
              &.current {
                color: $theme-color-2;
                border-color: $theme-color-2;
              }
              &:not(.current) {
                &:hover,
                &:active {
                  a {
                    color: #fff;
                    &:before {
                      border-color: $theme-color-2;
                      background: $theme-color-2;
                    }
                  }
                }
              }
              a {
                display: block;
                text-decoration: none;
                color: $textcolor-2;
                transition: color 0.2s ease-in-out;
                z-index: 1;
                position: relative;
                cursor: pointer;
                &:before {
                  content: '';
                  position: absolute;
                  transition: border-color 0.2s ease-in-out, background-color 0.2s ease-in-out;
                  border: 1px solid $textcolor-2;
                  border-radius: 8px;
                  background: #fff;
                  top: 50%;
                  left: 50%;
                  height: calc(100% + 10px + 1px);
                  width: calc(100% + 12.4px + 1px);
                  z-index: -1;
                  transform: translate(-50%, -50%);
                }
              }
              &[data-letter='all'] {
                border-radius: 23px;
                padding: 5px 15px;
                a {
                  &:before {
                    width: calc(100% + 30px);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .block.catal-results {
    margin-top: 40px !important;
    padding-bottom: 23px;
    padding-top: 23px;
    border-bottom: 1px solid rgba($lightgrey-1, 0.3);
    border-top: 1px solid rgba($lightgrey-1, 0.3);
    @include mq_min_width($tablet-medium) {
      padding-top: 0;
      margin-top: 32px !important;
      border-top: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .catal-results-title {
      margin: 0 0 15px;
      padding: 0;
      border: 0;
      font-weight: 300;
      line-height: 31px;
      font-size: 20px;
      color: $textcolor-2;
      @include mq_min_width($tablet-medium) {
        margin: 0;
      }
      .catal-results-nb {
        font-family: $font-1-alt;
        font-size: 22px;
      }
    }
    .catal-results-selectboxes {
      align-items: center;
      margin: 0;
      .catal-results-number,
      .catal-results-display {
        display: none;
      }
      .catal-results-sorting {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        label {
          padding: 0;
          margin: 0 12px 0 0;
          flex-shrink: 0;
          flex-grow: 0;
        }
      }
    }
  }
  .block.social-sharing {
    display: none;
  }
  .catal-ex-alone {
    margin: 32px 0 0;
  }
  .block.catal-ex {
    margin: 40px 0 -30px !important;
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: wrap;
    @include mq_min_width($desktop-small) {
      margin: 23px 0 0 !important;
      display: block;
    }
  }
  .catal-ex-item {
    margin: 0;
    padding: 0;
    border: 0;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    box-shadow: 0 4px 21px 0 rgba(0, 0, 0, 0.07);
    @include mq_min_width($desktop-small) {
      flex-direction: row;
      align-items: stretch;
      justify-content: space-between;
      padding: 30px 0 0;
      box-shadow: none;
      border-bottom: 1px solid rgba($lightgrey-1, 0.3);
    }
    &:not(.is-top-premium) {
      width: 100%;
      margin-right: 0 !important;
      @include mq_min_width($tablet-medium) {
        width: calc((100% - 30px) / 2);
        margin-bottom: 30px;
        &:nth-child(2n) {
          margin-right: 30px !important;
        }
      }
      @include mq_min_width($desktop-small) {
        width: auto;
        margin-bottom: 0;
        &:nth-child(2n) {
          margin-right: 0 !important;
        }
      }
    }
    &.is-top-premium {
      background: $theme-color-1;
      border: 0;
      @include mq_min_width($desktop-small) {
        padding: 30px 30px 38px;
      }
      .catal-ex-item-content {
        padding-top: 38px;
        @include mq_min_width($desktop-small) {
          padding-top: 0;
        }
        @include mq_min_width($desktop-small) {
          padding-bottom: 0;
        }
      }
      .catal-ex-item-tag {
        height: 38px;
        left: 50%;
        bottom: auto;
        top: 175px;
        width: calc(100% - 38px);
        transform: translateX(-50%) translateY(-50%);
        max-width: 230px;
        background: #fff;
        z-index: 1;
        @include mq_min_width($desktop-small) {
          left: 30px;
          bottom: 0;
          top: auto;
          width: calc(23.18 / 100 * (100% - 60px));
          transform: none;
          max-width: none;
          background: $lightgrey-3;
        }
        .catal-ex-item-tag-premium {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
          background: transparent;
          color: $textcolor-2;
          font-size: 10px;
          font-family: $font-2;
          font-weight: 500;
          padding: 0;
          letter-spacing: 0.87px;
        }
      }
      .catal-ex-item-img {
        a {
          @include mq_min_width($desktop-small) {
            background: #fff;
          }
        }
      }
    }
    .catal-ex-item-img {
      flex: 0 0 auto;
      margin: 0;
      width: 100%;
      min-height: 0;
      @include mq_min_width($desktop-small) {
        width: 23.18%;
        min-height: 194px;
        margin: 0 4.48% 0 0;
      }
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        background: $lightgrey-3;
        min-height: 0;
        height: 175px;
        overflow: hidden;
        @include mq_min_width($desktop-small) {
          height: 194px;
        }
        img {
          width: auto;
          height: auto;
          max-width: 100%;
          max-height: 100%;
          min-height: 0;
          min-width: 0;
          object-fit: cover;
          font-family: 'object-fit: cover;';
          transition: transform 0.5s ease-in-out;
          filter: none;
          opacity: 1;
          @include mq_min_width($desktop-small) {
            width: 100%;
            height: 100%;
          }
        }
        &:hover,
        &:active {
          img {
            transform: scale(1.08);
          }
        }
      }
    }
    .catal-ex-item-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      padding: 19px 20px 16px;
      @include mq_min_width($desktop-small) {
        @include clearfix;
        padding: 0 0 30px;
        display: block;
      }
      .catal-ex-item-group-title {
        margin: 0;
        min-height: 0;
        padding: 0;
        border: 0;
        order: 1;
        @include mq_min_width($desktop-small) {
          padding: 0 30px 0 0;
          float: left;
          width: 40%;
        }
        .catal-ex-item-buttons {
          display: none;
        }
        .catal-ex-item-title {
          font-size: 18px;
          line-height: 28px;
          font-weight: 900;
          color: $textcolor-2;
          font-family: $font-2;
          a {
            color: inherit !important;
            text-decoration: none;
            &:hover,
            &:active {
              text-decoration: underline;
            }
          }
        }
        .catal-ex-item-country {
          display: none;
        }
      }
      .catal-ex-item-cat {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        order: 3;
        float: none;
        padding: 0;
        margin: 0;
        @include mq_min_width($desktop-small) {
          border-left: 1px solid rgba($lightgrey-1, 0.3);
          padding: 0 0 0 30px;
          border-top: 0;
          margin: 0;
          width: 60%;
          float: right;
        }
        .catal-ex-item-cat-iconlist {
          padding-top: 20px;
          margin-top: 0 !important;
          width: 100%;
          border-top: 1px solid rgba($lightgrey-1, 0.3);
          @include mq_min_width($desktop-small) {
            padding-top: 0;
            margin-top: 0 !important;
            width: auto;
            border: 0;
          }
        }
        .catal-ex-item-cat-title {
          order: 1;
          margin: 0 0 10px;
          color: $textcolor-2;
          font-family: $font-2;
          font-size: 18px;
          font-weight: 900;
          line-height: 26px;
          padding-top: 20px;
          margin-top: 20px !important;
          width: 100%;
          border-top: 1px solid rgba($lightgrey-1, 0.3);
          @include mq_min_width($desktop-small) {
            padding-top: 0;
            margin-top: 0 !important;
            width: auto;
            border: 0;
          }
        }
        .catal-ex-item-cat-list {
          order: 2;
          .catal-ex-item-cat-item {
            margin: 0;
            line-height: 26px;
            font-size: 16px;
            font-weight: 300;
            color: $textcolor-2;
            &:before {
              content: '#';
              margin-right: -3px;
            }
            img {
              display: none;
            }
            a {
              color: inherit;
              text-decoration: none;
              &:hover,
              &:active {
                text-decoration: underline;
              }
            }
            &:not(:last-of-type) {
              margin-bottom: 6px;
            }
          }
        }
        .catal-ex-item-cat-more {
          order: 3;
          float: none;
          padding: 0;
          margin-top: 15px;
          .catal-ex-item-cat-more-link {
            @extend %button-underline;
            span {
              text-decoration: none;
            }
            .icon-catal {
              display: none;
            }
          }
        }
      }
      .catal-ex-item-desc {
        margin: 0 !important;
        padding: 0 !important;
        border: 0 !important;
        order: 2;
        float: none;
        width: 100%;
        @include mq_min_width($desktop-small) {
          float: left;
          width: 40%;
          padding: 0 30px 0 0 !important;
        }
        .catal-ex-item-country {
          color: $textcolor-2;
          margin-top: 5px;
          font-family: $font-2;
          font-size: 12px;
          font-weight: 500;
          letter-spacing: 1.04px;
          line-height: 15px;
        }
        .catal-ex-item-txt {
          font-size: 13px;
          margin: 15px 0 0;
        }
        .catal-ex-item-stand {
          margin-top: 15px;
          font-size: 14px;
        }
      }
    }
  }
  &.marque {
    .catal-ex-item {
      .catal-ex-item-content {
        .catal-ex-item-group-title {
          padding: 0;
          width: 100%;
          float: none;
          margin-bottom: 15px;
        }
        .catal-ex-item-desc {
          padding: 0 !important;
          float: none;
          width: 100%;
          .catal-ex-item-exhibitor {
            align-items: center;
            justify-content: flex-start;
            .catal-ex-item-exhibitor-title {
              margin-right: 10px;
              color: $textcolor-2;
              font-family: $font-2;
              font-weight: 900;
              line-height: 26px;
              font-size: 16px;
            }
            .catal-ex-item-exhibitor-list {
              flex-grow: 0;
              flex-shrink: 0;
              .catal-ex-item-exhibitor-item {
                margin: 0;
                padding: 0;
                a {
                  color: $textcolor-2;
                  text-decoration: none;
                  text-transform: uppercase;
                  font-family: $font-2;
                  font-weight: 500;
                  letter-spacing: 1.04px;
                  &:hover,
                  &:active {
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &.produit {
    .catal-ex-item {
      .catal-ex-item-content {
        .catal-ex-item-group-title {
          padding: 0;
          width: 100%;
          float: none;
          margin-bottom: 15px;
        }
        .catal-ex-item-cat {
          width: 100%;
          float: none;
          height: auto;
          padding: 0;
          border: 0;
          margin: 0;
          .catal-ex-item-cat-iconitem {
            color: $textcolor-2;
            text-transform: uppercase;
            font-family: $font-2;
            font-weight: 500;
            letter-spacing: 1.04px;
            .icon-catal {
              margin-right: 0;
            }
          }
        }
        .catal-ex-item-desc {
          display: none;
        }
      }
    }
  }
}
